.nodesystem-transform {
  position: relative;
  height: 100%;
  width: 100%;
}
.nodesystem-transform > .nodes-container {
  position: relative;
  width: 0px;
  height: 0px;
  left: 50%;
  top: 50%;
  overflow: visible;
  z-index: 1;
