    left: -5000vw;
    background-color: white;
    opacity: 0.2;
    z-index: -1;
  } */
}
.nodesystem-transform > svg {
  position: absolute;
  pointer-events: none;
  overflow: visible;
  width: 1px !important;
  height: 1px !important;
  left: 50%;
  top: 50%;
  z-index: 0;
}
.nodesystem-transform > svg > path {
  stroke-linecap: round;
}

.nodesystem-wrapper {
  height: 100%;
  width: 100%;
  overflow: hidden;
  --node-editor-bg: var(--text-color, #303030);
  --node-editor-grid-size: 1.5rem;
  --node-editor-grid-width: 1px;
  background-image: radial-gradient(#ffffff11 var(--node-editor-grid-width), transparent var(--node-editor-grid-width));
  background-size: var(--node-editor-grid-size) var(--node-editor-grid-size);
}
.nodesystem-wrapper.nodesystem-state-dragging {
  cursor: grab;
}