.node-wrapper {
  position: absolute;
  background-color: var(--foreground-color, #303030);
  border-radius: 5px;
  box-sizing: content-box;
  min-height: 50px;
  max-width: 200px;
  color: var(--text-color, #f0f0f0);
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.13), 1px 2px 2px rgba(0, 0, 0, 0.1), -1px -2px 2px rgba(0, 0, 0, 0.05);
}
.node-wrapper > .node-title {
  color: var(--text-color, #f0f0f0);
  pointer-events: none;
  user-select: none;
  white-space: pre;
  font-size: 1em;
  margin: 0;
  padding: 2px;
  padding-left: 4px;
  opacity: 1;
  border-bottom: solid 2px #469c6e;
  border-radius: 4px 4px 0px 0px;
}
.node-wrapper.node-active {
  box-shadow: 0px 0px 0px 1px white;
}
.node-wrapper.node-dragging {
  pointer-events: none;
  box-shadow: 0px 0px 0px 1px white;
}
.node-wrapper.node-selected {
  box-shadow: 0px 0px 0px 1px #469c6e;
}

.node-outputs-wrapper {
  width: 0px;
  right: 0px;
  top: 2px;
  position: absolute;
}

.socket-type-all {
  background: conic-gradient(red, orange, yellow, green, blue, purple);
}