html {
  height: 100%;
}

input,
select,
label {
  padding-left: 0;
  margin-top: 0;
  padding-top: 0;
  margin-bottom: 0;
  padding-bottom: 0;
  background: transparent;
  color: var(--outline-color);
  border: none;
}

label {
  font-size: 0.5em;
}

.nodesystem-wrapper {
  position: relative;
  height: 100%;
  background: var(--background-color, #1a1a1a);
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
  /* Chrome all / Safari all */
  -moz-user-select: none;
  /* Firefox all */
  -ms-user-select: none;
  /* IE 10+ */
  user-select: none;
  /* Likely future */
}